@import '_var.scss';

/* оформление содержимого шаблона, которое находится под
header */
.block {
	padding: 60px 0;
}

.title_block {
  @include font-size(30px);
	text-align: center;
	margin-bottom: 10px;
	text-transform: uppercase;
}
.title_block_descr {
  @include font-size(16px);
	text-align: left;
	margin-bottom: 20px;
	text-transform: uppercase;
font-weight: normal;
line-height: 1.53;
border-bottom: 1px solid #eee;

}
.cont_title {
	color: red;
}
.padding_block {
	height: 200px;
	background-color: red;
	}
.block_h {
	height: 300px;
}

.grey_block {
	background-color: grey;
}
.blue_block {
	background-color: blue;
}
.Aqua_block {
	background-color: Aqua;

}

.BlueViolet_block {
	background-color: BlueViolet;

}

.Chocolate_block {
	background-color: Chocolate;


}

.b_w {
padding-left: 10px;
padding-right: 10px;
}


.submit_bb {
text-transform: uppercase;
display: block;
margin: 0 auto;
text-align: center;
background-color: transparent;
border: 3px solid #fff;
color:#000;
margin-bottom: 50px;
cursor: pointer;
 font-size: 1.4rem;
 padding: 9px 33px;
 box-shadow: 1px 1px 1px #95989A;
 margin-bottom: 10px;
 @include trans;
}

.submit_bb:hover {
box-shadow:none;
 }



